import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import {
  Card,
  CardHeader,
  CardImg,
  CardBody,
  CardTitle,
  Button,
  CardFooter,
  Row,
  Col,
} from "shards-react"

export default function Member({ pageContext: { member, type, url } }) {
  const { name, pname, slug, photo, roles, linkedin, website } = member
  const keywords = [name, ...roles].join(",")
  const desc = `We would like to introduce one of our members such as eng. ${name} to you. take a look at ${name} page at brilacasck about page`
  const title = `${name} | ${pname} | About Brilacasck Member`
  const richSnippet = `{ 
      "@context": "https://schema.org", 
      "@type": "Article",
      "headline": "Oh, Read About ${name}",
      "alternativeHeadline": "This article is about Brilacasck member > ${name}",
      "image": "http://about.blilac.ir${photo}",
      "author": "Alireza Kavian", 
      "award": "Best member ever could had > ${name}",
      "editor": "${name}", 
      "genre": "member of computer engineering/science team", 
      "keywords": "${keywords}", 
      "publisher": {
        "@type": "Organization",
        "name": "Brilacasck",
        "logo": {
          "@type": "ImageObject",
          "url": "https://brilacasck.ir/brilacasck/public/logo/BRILACASCK.png"
        }
      },
      "url": "${url}",
      "mainEntityOfPage": {
        "@type": "WebPage",
        "@id": "https://about.blilac.ir"
      },
      "datePublished": "2019-10-27",
      "dateCreated": "2018-10-20",
      "dateModified": "2019-10-27",
      "description": ${desc},
      "articleBody": "Read about a Brilacasck member >> ${name} > ${keywords} > ${slug}"
    }`
  return (
    <Layout>
      <SEO title={title} description={desc} keywords={keywords} />
      <div>
        <div className="snippet">
          <script
            type="application/ld+json"
            dangerouslySetInnerHTML={{ __html: richSnippet }}
          ></script>
        </div>
        <div className="member" style={{ margin: "25px 0" }}>
          <Row>
            <Col sm={12} md={8} lg={6} className="mx-auto">
              <Card>
                <CardHeader style={{ fontWeight: 700 }}>{name}</CardHeader>
                <CardImg src={photo} alt={name} />
                <CardBody>
                  <CardTitle>Roles</CardTitle>
                  <ul>
                    {roles.map(role => {
                      return <li>{role}</li>
                    })}
                  </ul>
                  <Button>
                    <a
                      style={{ textDecoration: "none", color: "white" }}
                      href={website}
                    >
                      Personal Website
                    </a>{" "}
                    &rarr;
                  </Button>
                </CardBody>
                <CardFooter>
                  <a
                    style={{ textDecoration: "none", color: "blue" }}
                    href={linkedin}
                  >
                    Linkedin
                  </a>
                </CardFooter>
              </Card>
            </Col>
          </Row>
        </div>
      </div>
    </Layout>
  )
}
